<template>
    <div role="tablist">

        <div class='container technologies row'>
            <div class='technologies-card col-sm-4' id='card-1'>
                <!-- <a href='/FaceRecognition'> -->
                <b-card id='card-1'
                @click='scrollToTechnologies'
                v-b-toggle.accordion-1
                bg-variant="transparent"
                border-variant="transparent"
                overlay
                no-body
                img-src='../../assets/FaceRecognition/Card-1.png'
                ></b-card>
                <!-- </a> -->
            </div>
            <div class='technologies-card col-sm-4' id='card-2'>
                <!-- <a href='/UnmannedStore'> -->
                <b-card id='card-2'
                @click='scrollToTechnologies'
                v-b-toggle.accordion-2
                bg-variant="transparent"
                border-variant="transparent"
                overlay
                no-body
                img-src='../../assets/UnmannedStore/Card-1.png'></b-card>
                <!-- </a> -->
            </div>
            <div class='technologies-card col-sm-4' id='card-3'>
                <!-- <a href='/ALPR'> -->
                <b-card id='card-3'
                @click='scrollToTechnologies'
                v-b-toggle.accordion-3
                bg-variant="transparent"
                border-variant="transparent"
                overlay
                no-body
                img-src='../../assets/ALPR/Card-1.png'></b-card>
                <!-- </a> -->
            </div>
        </div>

        <b-card 
        bg-variant="dark"
        border-variant="transparent"
        body-bg-variant='transparent'
        body-border-variant='transparent'
        overlay
        no-body
        class='expanded-technologies'>

            <b-collapse id="accordion-1"  accordion="my-accordion">
                <b-card-body>
                <!-- <b-card-text>FACE RECOGNITION PART</b-card-text> -->
                <fr-page></fr-page>
                </b-card-body>
            </b-collapse>

            <b-collapse id="accordion-2"  accordion="my-accordion">
                <b-card-body>
                <!-- <b-card-text>UNMANNED STORE PART</b-card-text> -->
                <unmanned-page></unmanned-page>
                </b-card-body>
            </b-collapse>

            <b-collapse id="accordion-3"  accordion="my-accordion">
                <b-card-body>
                <!-- <b-card-text>ALPR PART</b-card-text> -->
                <alpr-page></alpr-page>
                </b-card-body>
            </b-collapse>
        
        </b-card>
        
    </div>
</template>

<script>

import FaceRecognitionPage from './FaceRecognitionPage/FaceRecognitionPage'
import UnmannedStorePage from './UnmannedStorePage/UnmannedStorePage'
import ALPRPage from './ALPRPage/ALPRPage'

export default {
    data() {
        return {
            selectedCard: ''
        }
    },
    components: {
        'fr-page': FaceRecognitionPage,
        'unmanned-page': UnmannedStorePage,
        'alpr-page': ALPRPage
    },
    methods: {
        scrollToTechnologies() {
            
            var el = this.$el.getElementsByClassName("expanded-technologies")[0];
            setTimeout(function(){ 
                el.scrollIntoView({behavior: "smooth", block: "center", inline: "center"})
                }, 500);
        }
    }
}

</script>

<style scoped>

.technologies {
    color: black;
    /* background-color: #343a40; */
    background-color: rgba(52,58,64,1);
    max-width: 100%;
    /* height: 35vh; */
    /* margin: 5vh 0 20vh 0; */
    margin: auto;
    padding: 0 20vw 0 20vw;
    font-family: Raleway;
}  .technologies .technologies-card {
      padding: 1vh 2vw 1vh 2vw;
    }
   .technologies .technologies-card:hover {
      /* margin: 0vh 1vw 0vh 1vw; */
      background-color: rgba(150,150,150,0.8);
      cursor: pointer;
      padding: 1vh 2vw 1vh 2vw;
    }

.technologies .selected {
    background-color: #343a40;
}

</style>